.popover {
  font-family: inherit;
}

/*** WRAPPER ***/
#wrapper {
  .banner {

  }
  .breadcrumb {
    background: transparent;
    padding: 0;
    display: block;

    &[data-depth="1"] {
      display: none;
    }
    ol {
      padding-left: 0;
      margin-bottom: 0;
    }
    li {
      display: inline;
      &::after {
        content: "/";
        opacity: 0.4;
        margin: .3em;
      }
      &:last-child {
        &::after {
          content: "";
        }
      }
    }
    a:hover {
      opacity: 0.8;
    }
    #iqitproductsnav{
      font-size: 1.5rem;
    }
  }
}
/*** MAIN ***/
#main {
  .page-content {
    margin-bottom: rem(25px);
    h6 {
      margin-bottom: rem(18p);
    }
  }
}


#notifications {
  ul {
    margin-bottom: 0;
  }
}

.block-section{
  margin-bottom: 3rem;
  clear: both;
}

.section-title{
  position: relative;
}

/*** THEME COMPONTENTS ***/
#back-to-top{
  border: none;
  cursor: pointer;
  width: 2.5rem;
  height: 2.5rem;
  padding: 0;
  opacity: 0;
  visibility: hidden;
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  transition: $default-transition;
  font-size: 2rem;
  outline: none;
  z-index: 120;
  &.-back-to-top-visible{
    opacity: 1;
    visibility: visible;
  }
}

#page-preloader{
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background: white;
}

/*** Responsive part ***/
@include media-breakpoint-down(sm) {
  #checkout-cart-summary {
    float: none;
    width: 100%;
    margin-top: 1rem;
  }
  #header {
  }
  section.checkout-step {
    width: 100%;
  }
  .default-input {
    min-width: 100%;
  }
  label {
    clear: both;
  }
}
@include media-breakpoint-down(md) {
  .container {
    max-width: 100%;
  }
}



/*** OLD BS-fixes***/
#_mobile_blockcart-content.dropdown-menu,
#_mobile_iqitmegamenu-mobile.dropdown-menu{
border: none !important;
  background: none !important;
}
.card-block {
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  padding: 1.25rem;
}
.col-xs-12{
  @extend .col-12;
}

.float-xs-left  { @extend .float-left; }
.float-xs-right { @extend .float-right; }
.float-xs-none  { @extend .float-none; }

@each $bp in map-keys($grid-breakpoints) {
  .hidden-#{$bp}-up {
    @include media-breakpoint-up($bp) {
      display: none !important;
    }
  }
  .hidden-#{$bp}-down {
    @include media-breakpoint-down($bp) {
      display: none !important;
    }
  }
}


.flex-first {
  -webkit-box-ordinal-group: 0;
  -webkit-order: -1;
  -ms-flex-order: -1;
  order: -1;
}

.flex-last {
  -webkit-box-ordinal-group: 2;
  -webkit-order: 1;
  -ms-flex-order: 1;
  order: 1;
}


.input-group-addon, .input-group-btn, .input-group .form-control {
  -js-display: flex;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.input-group-addon, .input-group-btn {
  white-space: nowrap;
  vertical-align: middle;
}
.input-group-btn {
  position: relative;
  font-size: 0;
  white-space: nowrap;
}

.input-group-btn>.btn {
  position: relative;
  flex: 1;
}

