/*** 5 column layout ***/
.col-15,
.col-sm-15,
.col-md-15,
.col-lg-15,
.col-xl-15{
  position: relative;
  width: 100%;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
}
.col-15 {
  flex: 0 0 20%;
  max-width: 20%;
}
@include media-breakpoint-only(sm) {
  .col-sm-15 {
    flex: 0 0 20%;
    max-width: 20%;
  }
}
